import React from 'react';
import '../styles/projectDetails.css';

interface detailsProps {
	close: any;
	project: {
		title: string;
		summary: string;
		bgColor: string;
		textColor: string;
		description: any;
	};
}

const projectDetails = (props: detailsProps) => {
	const style = {
		background: `${props.project.bgColor}`,
		color: `${props.project.textColor}`,
	};

	return (
		<div className='projectDetails'>
			<div className='projectDetails-content' style={style}>
				<button
					className='close-btn'
					onClick={() => {
						props.close(false);
					}}>
					X
				</button>
				<div
					className='projectDetails-content-text'
					dangerouslySetInnerHTML={{ __html: props.project.description }}
				/>
			</div>
		</div>
	);
};

export default projectDetails;
